suspend fun hashFiles(vararg paths: String) {
    val globber = create(paths.joinToString("\n")).await()
    val fileNames = globber.glob().await()
    fileNames.sort()

    for (name in fileNames) {
        info("file: $name")
    }
    info("done hash: ${fileNames.size}")
//    val hash = createHash("sha256")
