package com.github.burrunan.gradle

import domain.create
import github.actions.core.*
import kotlinx.coroutines.await
import kotlinx.coroutines.coroutineScope
import kotlinx.coroutines.delay
import process
import kotlin.js.Promise

const val RefKey = "GITHUB_REF"

fun isValidEvent() = !process.env[RefKey].isNullOrBlank()

suspend fun main() {
    if (!isValidEvent()) {
        warning("$RefKey is needed for the action to work. GitHub event was ${process.env["GITHUB_EVENT_NAME"]}")
        return
    }

    if (getState("PRE").isBlank()) {
        saveState("PRE", "Y")
        pre()
    } else {
        post()
    }
}

suspend fun pre() {
    info("Caching generated-gradle-jars")
    hashFiles("gradle/wrapper/gradle-wrapper.properties")
}

suspend fun post() {
    info("running post")
}
